class User {
  constructor (name, role) {
    this.name = name
    this.role = role
  }
}

export default {
  state: {
    user: null
  },
  mutations: {
    authUser (state, payload) {
      state.user = payload
    }
  },
  actions: {
    isUserAuth ({ commit }) {
      if (localStorage.getItem('skfUserInfo') !== null) {
        const { name, role } = JSON.parse(localStorage.getItem('skfUserInfo'))
        commit('authUser', name, role)
      } else {
        return false
      }
    },
    async authUser ({ commit }, payload) {
      // делаем запрос к бд с login/password
      commit('clearError')
      commit('setLoading', true)
      return await new Promise((resolve, reject) => {
        setTimeout(() => {
          if (payload.login === 'skf-mtusi' && payload.password === 'skf-mtusi') {
            commit('authUser', new User('Студент', 'student'))
            commit('setLoading', false)
            resolve('Success auth')
          } else {
            commit('setLoading', false)
            commit('setError', 'Пользователь не найден')
            const e = 'Пользователь не найден'
            reject(e)
          }
        }, 2000)
      })
    }
  },
  getters: {
    user: state => state.user
  }
}
